.sideBar {
  border-right: 1px solid rgb(223, 223, 223);
  display: flex;
  flex-direction: column;
  height: 100vh;
  width: 244px;
  background-color: rgb(255, 255, 255);
  box-sizing: border-box;
  z-index: 1;
  padding: 8px 12px 20px 12px;
  transition: 0.5s;
  position: sticky;
  top: 0;
  &.true {
    width: 72px;
    .side-bar-span {
      display: none;
    }
    .App-logo {
      height: 24px;
      width: 24px;
      display: block;
    }
    .App-word {
      display: none;
    }
  }
  .logo {
    margin-top: 10px;
    margin-bottom: 19px;
  }
  .menu {
    flex: 1 0 auto;
  }
  ul {
    list-style: none;
    padding: 0;
    margin: 0;
    li {
      cursor: pointer;
      padding: 12px;
      margin: 8px 0 8px 0;
      font-size: 16px;
      line-height: 24px;
      text-overflow: ellipsis;
      white-space: nowrap;
      display: flex;
      flex-direction: row;
      align-items: center;
      span {
        padding-left: 16px;
      }
      .profile-icon {
        border-radius: 50%;
        height: 24px;
      }
    }
  }
  .side-bar-icon {
    height: 24px;
    width: 24px;
  }
  .side-bar-li {
    &:hover {
      background-color: rgba(250, 250, 250, 0.984);
      border-radius: 24px;
    }
    &:hover .side-bar-icon {
      transform: scale(1.1);
    }
    &.isOpen {
      animation: fade-in 0.5s forwards;
    }
    &.isClose {
      animation: fade-out 0.5s forwards;
    }
  }
  a {
    text-decoration: none;
    color: inherit;
  }
  a:hover {
    text-decoration: none;
    cursor: pointer;
  }
  .App-logo {
    display: none;
  }
  .more-menu {
    display: block;
  }

  @media only screen and (max-width: 1200px) {
    width: 72px;
    height: 100vh;
    .side-bar-span {
      display: none;
    }
    .App-logo {
      height: 24px;
      width: 24px;
      display: block;
    }
    .App-word {
      display: none;
    }
  }

  @media only screen and (max-width: 600px) {
    display: none;
  }
}
